//Suponha que seu site tenha 10 pginas com o mesmo rodap em todos eles. Se um dia voc quiser mudar o rodap do site, voc ter que mudar em todas as 10 pginas. Pra isso serve os components, para facilitar.

//Importa React de react.
import React from "react";
//Importa o css especfico do index. O Global est em App.css

import logo from '../../assets/img/logo.jpeg'

import "./rodape.css"


import {Col} from 'react-bootstrap';

const Rodape =()=>{
    return(
        <div style={{backgroundColor : '#343A40'}}>
            <container className='rodape' >
                <div>
               <a href="/Carongo-Web/"> <Col sm={8}><img alt="Carongo" src={logo} style={{ width : '175px'}} /></Col></a>
                </div>
            </container>
            <div className='text-center'>
                <h2>Senai de Informatica - 2021</h2>
            </div>
   
    
    
  
    
 
    
     
  
 

       
        </div>
    )
}

export default Rodape;